@import "wordpresscore";

.overlayBody {
  position: fixed;
  z-index: 2;
  display: none;
  background: rgba(0, 0, 0, .6);
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  cursor: pointer;
}

[class^='clb'] {
  clear: both;
  display: block;
}

.clb-header {
  height: 62px;
}

.clb {
  clear: both;
  display: block;
}

.clb5 {
  height: 5px;
}

.clb10 {
  height: 10px;
}

.clb20 {
  height: 20px;
}

.clb30 {
  height: 30px;
}

.clb40 {
  height: 40px;
}

.h500 {
  height: 500px;
}

.p0 {
  padding: 0 !important;
}

.round-0 {
  border-radius: 0 !important;
}

.mg0 {
  margin: 0;
}

.bg1 {
  background: $c1;
}

.bg2 {
  background: $c2;
}

.bg3 {
  background: $c3;
}

.bg4 {
  background: $c4;
}

.c1 {
  color: $c1;
  font-weight: bold;
}

.c2 {
  color: $c2;
}

.c3 {
  color: $c3;
}

.c4 {
  color: $c4;
}

.bg-gray {
  background: $c-gray;
}

