/* /* =========================================================================================================== * /*/

.w1 {
  width: 100% !important;
}

.wa {
  width: auto !important;
  max-width: 100%;
}

a {
  text-decoration: none;
  &:hover {
    text-decoration: none;
  }
}

img {
  max-width: 100%;
}

ul {
  list-style: none;
  padding-left: 0;
  margin: 0;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

.icon {
  background: url(../img/sprites.png);
}

.db {
  display: block;
}

.preline {
  white-space: pre-line;
}

.relative {
  position: relative;
}

.fll {
  float: left;
}

.flr {
  float: right;
}

.block-center {
  display: block;
  text-align: center;
}

/* end general */
.container {
  padding: 0 15px;
  @include mq(992, max) {
    padding: 0 30px;
  }
  margin: auto;
}

//media_____________________
.show-sp {
  @include mq(768) {
    display: none;
  }
}

.show-pc {
  @include mq(767, max) {
    display: none;
  }
}

.hidden-sp {
  @include mq-mix(0, 992) {
    display: none;
  }
}

.hidden-pc {
  @include mq(992) {
    display: none;
  }
}

@if $w_con>1170 {
  @media(min-width: ($w-con)+30px) {
    .container {
      width: $w-con + px;
    }
  }
} @else {
  @media(min-width: 1200px) {
    .container {
      width: $w-con + px;
    }
  }
}
